<script>
import Bubble from "@/components/chat/Bubble.vue";
import Button from "@/components/button.vue";
import Input from "@/components/input.vue";
import {useToast} from "vue-toastification";
import ProfilePicture from "@/components/ProfilePicture.vue";
import chatDropdown from "@/components/chat/chatDropdown.vue";
import Dropdown from "@/components/Dropdown.vue";
import SearchInput from "@/components/searchInput.vue";

export default {
  name: "chatBox",
  components: {SearchInput, Dropdown, chatDropdown, ProfilePicture, Input, Button, Bubble},
  props: ['messages', 'profile', 'isModal'],
  data(){
    return {
      sendMessageParam: {
        text: '',
        image: null
      },
      Reply: false,
      reply_message: null,
      message_id: null,
      searchMessage: '', // Initialize searchMessage here
      localMessages: [],// Local copy of messages
      closeChatbox:true,
    }
  },
  mounted() {
    console.log('profile is: \n', this.profile)

    // Initialize WebSocket connection
    this.Socket = new WebSocket('ws://' + window.location.host + '/ws/supportMessages/');
    this.Socket.onopen = function(event) {
      console.log('WebSocket connection opened');
    };
    // Set up message handler
    this.Socket.onmessage = (e) => {
      const data = JSON.parse(e.data);
      if (data.message) {
          // Handle incoming messages from the server
          let message = JSON.parse(data.message)
          console.log('Received message:', message);

          let enter_id = message['sender_profile']['enter_id']

          // console.log('enter id is ', enter_id)
          // console.log('user_id is: ', this.profile['enter_id'])

          if(enter_id == this.profile['enter_id']){

            this.localMessages.push(message)
            console.log(this.localMessages);
          }
      }
    };

    // Handle WebSocket errors
    this.Socket.onerror = (error) => {
      console.error('WebSocket Error: ', error);
    };
  },
  beforeDestroy() {
    // Close WebSocket connection when component is destroyed
    if (this.Socket) {
      this.Socket.close();
    }
  },
  methods: {
    onToggle() {
      this.closeChatbox=false
      this.$emit('closeChatbox', this.closeChatbox);
      // console.log('chatbox closed.')
    },
    onFilePicked (event) {
      const files = event.target.files
      // Send the image file to the server
      const image = new FormData();
      image.append("image", files[0]); // Add image to form data
      this.sendMessageParam.image = image
    },
    sendMessage(){
      console.log('send messsage')
      const user_id = this.profile.user_id
      const id = this.profile.id
      // const answer = {
      //   'answer': this.sendMessageParam.text,
      //   'answer_picture': this.sendMessageParam.image
      // }
      // this.messages.unshift(answer)
      // this.localMessages.unshift(answer)
      this.localMessages.forEach((item, index) => {
        // console.log(item, item['id'] , this.message_id, item['message_id'] == this.message_id)
        if (item['id'] == this.message_id){
          // console.log('it is equel\n', item)
          item['answer'] = this.sendMessageParam.text;
          item['answer_picture'] = this.sendMessageParam.image;
        }
      });
      const params = {
        "user_id": id,
        "id": id,
        "message": this.sendMessageParam.text,
        "message_id": this.message_id
      };
      if (this.sendMessageParam.image){
        this.axios.post('/api/sendMessageWithImage', this.sendMessageParam.image, {
          headers: {
            'Content-Type': 'multipart/form-data' // Set content type for image upload
          },
          params: params
        }).then((response) => {
          const toast = useToast();
          toast.success("پیام شما با موفقیت ارسال شد.");
        })
      } else {
        this.axios.get('/api/sendMessage',{params: params}).then((response) => {
          const toast = useToast();
          toast.success("پیام شما با موفقیت ارسال شد.");
        })
      }
      this.Reply = false
      this.sendMessageParam.text = ''
    },
    deleteMessage(id){
        const params = {
          "id": id,
        };
        this.axios.get('/api/deleteMessage',{params: params}).then((response) => {
          const index = this.messages.findIndex(item => item.id === id);
          if (index > -1) {
            this.messages.splice(index, 1);
          }
          // Get toast interface
          const toast = useToast();
          toast.success('.پیام کاربر با موفقیت حدف شد'); // Display error toast notification
        })
    },
    BubbleEvent(event){
      console.log(event)
      const name = event[0]
      const message_id = event[1]

      switch (name) {
        case 'Reply':
          console.log('Reply')
          this.message_id = message_id
          this.reply_message = event[2]
          this.Reply = true
          break
        case 'Delete':
          this.deleteMessage(message_id)
          break
      }
    },
    sortBasedOnNewMessage(){
      this.messages.sort((a, b) => {
        // Sort by ID first (ascending)
        if (a.id < b.id) return -1;
        if (a.id > b.id) return 1;
        return 0; // No further sorting needed if no invest field or IDs are equal
      });
    },
    sortBasedOnOldMessage(){
      this.messages.sort((a, b) => {
        // Sort by ID first (ascending)
        if (a.id > b.id) return -1;
        if (a.id < b.id) return 1;
        return 0; // No further sorting needed if no invest field or IDs are equal
      });
    },
    chatBoxDropdownEvent(event){
      console.log(event[0]);
      const action = event[0]
      switch (action) {
        case 'جدیدترین پیام':
          this.sortBasedOnNewMessage();
          break;
        case 'قدیمی ترین پیام':
          this.sortBasedOnOldMessage();
          break;
      }
    },
  },
  watch: {
    searchMessage(searchedText) {
      // Convert searchedText to lowercase once for efficiency
      const lowerCasedSearchText = searchedText.toLowerCase();

      // Check if the searched text is empty
      if (!lowerCasedSearchText) {
        this.localMessages = [...this.messages]; // Create a shallow copy of messages
        return;
      }

      // Filter messages based on the search text
      this.localMessages = this.messages.filter(item =>
        item.message.toLowerCase().includes(lowerCasedSearchText)
      );
    },
    // Watch for changes in the messages prop to keep localMessages in sync
    messages: {
      handler(newMessages) {
        this.localMessages = [...newMessages];
        }, immediate: true // Ensure the handler runs immediately on component mount
    },
  },

}
</script>

<template>
  <div class="flex flex-col justify-between h-full ">
    <template v-if="profile">
      <nav class="bg-white shadow">
        <div class="flex items-center space-x-4 px-2 rtl:space-x-reverse max-w-screen-xl justify-between py-4">
           <div class="flex-shrink-0">
  <!--           <ProfilePicture :username="profile['enter_id']" :src="profile['profile_picture']"></ProfilePicture>-->
           </div>
           <div class="flex-1 min-w-0 mx-auto">
              <p class="text-sm font-medium text-gray-900 truncate dark:text-white">
                {{ profile['enter_name'] }}
              </p>
              <p class="text-sm text-gray-500 truncate dark:text-gray-400 max-w-sm">
                {{ profile['enter_id'] }}
              </p>
           </div>
           <div class="relative">
             <div class="absolute inset-y-0 start-0 flex items-center ps-3 pointer-events-none">
                <svg class="w-4 h-4 text-gray-500 dark:text-gray-400" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 20 20">
                      <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="m19 19-4-4m0-7A7 7 0 1 1 1 8a7 7 0 0 1 14 0Z"/>
                </svg>
            </div>
            <input v-model="searchMessage" type="search" id="default-search" class="block w-full p-4 ps-10 text-sm text-gray-900 border border-gray-300 rounded-lg bg-gray-50 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="جستوجو در پیام ها" required />
           </div>
           <div class="">
             <chatDropdown class="self-center" position-class="left-0" @dropdownEvent="chatBoxDropdownEvent" title="مرتب سازی" :options="['جدیدترین پیام', 'قدیمی ترین پیام']"></chatDropdown>
           </div>
           <button @click="onToggle" v-if="isModal" type="button" class="font-sans text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ms-auto inline-flex justify-center items-center">
              <svg class="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 14"><path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"></path></svg><span class="sr-only">Close chat</span>
            </button>
        </div>
      </nav>
      <div class="bg-gray-200 overflow-y-auto h-full">
        <!-- handle Bubbles-->
        <ul v-for="message in localMessages" :key="message.id">
          <li class="pb-3 sm:pb-4 px-4 py-2">
            <template v-if="message['message']">
              <Bubble
                  :datetime="message['datetime']"
                  :status="message['status']"
                  :picture="message['sender_profile']['picture']"
                  :enter_id="message['sender_profile']['enter_id']"
                  :enter_name="message['sender_profile']['enter_name']"
                  :message="message['message']"
                  :image="message['sender_picture']"
                  :isSender="true"
                  :messageID="message['id']"
                  @BubbleEvent="BubbleEvent"
              />
            </template>

            <template v-if="message['answer']">
              <Bubble
                  :datetime="message['datetime']"
                  enter_id="You"
                  enter_name="شما"
                  :message="message['answer']"
                  :image="message['answer_picture']"
                  :isSender="false"
              />
            </template>
          </li>
        </ul>
      </div>

<!--      send message box-->
    <form v-if="Reply">
      <label for="chat" class="sr-only">Your message</label>
      <!--      replay box-->
      <div class="sticky top-0 left-0 right-0 flex items-center px-3 py-2 bg-gray-50 border-b border-gray-200">
        <div class="flex items-center space-x-4 rtl:space-x-reverse">
           <div class="flex-shrink-0 inline-flex justify-center p-2 text-blue-600 rounded-full">
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-reply-fill" viewBox="0 0 16 16">
              <path d="M5.921 11.9 1.353 8.62a.72.72 0 0 1 0-1.238L5.921 4.1A.716.716 0 0 1 7 4.719V6c1.5 0 6 0 7 8-2.5-4.5-7-4-7-4v1.281c0 .56-.606.898-1.079.62z"/>
            </svg>
           </div>
           <div class="flex-1 min-w-0 mx-auto">
              <p class="text-sm font-bold text-gray-900 truncate dark:text-white">
                پاسخ به {{ profile['enter_name'] }}
              </p>
              <p class="text-sm text-gray-500 truncate max-w-[200px]">
                {{ reply_message }}
              </p>
           </div>
        </div>
        <button @click="Reply=false" type="button" class="font-sans text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ms-auto inline-flex justify-center items-center dark:hover:bg-gray-600 dark:hover:text-white">
          <svg class="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 14">
              <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"/>
          </svg>
          <span class="sr-only">Close modal</span>
        </button>
      </div>
      <div class="sticky top-0 left-0 right-0 flex items-center px-3 py-2 bg-gray-50 dark:bg-gray-700">
          <div class="flex ps-0 space-x-1 rtl:space-x-reverse sm:ps-2">
             <button
                 @click="$refs.fileInput.click()"
                 type="button" class="inline-flex justify-center items-center p-2 text-gray-500 rounded cursor-pointer hover:text-gray-900 hover:bg-gray-100 dark:text-gray-400 dark:hover:text-white dark:hover:bg-gray-600">
                 <svg class="w-4 h-4" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 20 18">
                    <path d="M18 0H2a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2Zm-5.5 4a1.5 1.5 0 1 1 0 3 1.5 1.5 0 0 1 0-3Zm4.376 10.481A1 1 0 0 1 16 15H4a1 1 0 0 1-.895-1.447l3.5-7A1 1 0 0 1 7.468 6a.965.965 0 0 1 .9.5l2.775 4.757 1.546-1.887a1 1 0 0 1 1.618.1l2.541 4a1 1 0 0 1 .028 1.011Z"/>
                 </svg>
                 <span class="sr-only">Upload image</span>
             </button>
              <input
                type="file"
                ref="fileInput"
                accept="image/*"
                @change="onFilePicked"
                class="hidden"
              />
         </div>
          <textarea
              id="chat"
              rows="1"
              class="block mx-4 p-2.5 w-full text-sm text-gray-900 bg-white rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-800 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
              placeholder="پیام خود را بنویسید..."
              v-model="sendMessageParam.text"
              required
          ></textarea>
          <button
              @click="sendMessage"
              type="button"
              class="inline-flex justify-center p-2 text-blue-600 rounded-full cursor-pointer hover:bg-blue-100 dark:text-blue-500 dark:hover:bg-gray-600">
          <svg class="w-5 h-5 rotate-90 rtl:-rotate-90" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 18 20">
              <path d="m17.914 18.594-8-18a1 1 0 0 0-1.828 0l-8 18a1 1 0 0 0 1.157 1.376L8 18.281V9a1 1 0 0 1 2 0v9.281l6.758 1.689a1 1 0 0 0 1.156-1.376Z"/>
          </svg>
          <span class="sr-only">Send message</span>
      </button>
      </div>
    </form>
    </template>
    <template v-else>
      <div class="flex justify-center items-center h-full">
        <p class="py-12 px-4 text-gray-600 bg-gradient-to-r from-slate-200 to-slate-300 rounded-md bg-clip-padding backdrop-filter backdrop-blur-sm bg-opacity-50">
          روی یکی از افراد لیست کلیک کنید تا پیام های آن نمایش داده شود.
        </p>
      </div>
    </template>
  </div>
</template>
