<script>
import Button from "@/components/button.vue";
import ChatBox from "@/components/chat/chatBox.vue";

export default {
  name: "chatboxModal",
  components: {ChatBox, Button},
  props: ['btnTitle', 'modalTitle', 'btnClass', 'messages', 'profile'],
  data() {
    return {
      isOpen: false
    };
  },

  computed: {
    isModalVisible() {
      return this.isOpen;
    }
  },

  methods: {
    onToggle() {
      this.isOpen = !this.isOpen;
      this.$emit('modalOpened', this.isOpen);
    },
    close(val){
      this.onToggle()
      console.log('chatbox closed.', val)
    }
  }
};
</script>

<template>
    <Button @click="onToggle" :class="btnClass">
      <template v-slot:title>
        {{ btnTitle }}
      </template>
    </Button>
    <!-- Render inside our `<div id="modals"></div>` in index.html -->
    <Teleport to="body">
      <transition name="fade">
      <!-- Show / hide the modal -->
      <div :class="[isModalVisible? '': 'hidden']">
        <!-- The backdrop -->
        <div @click="onToggle" class="fixed inset-0 z-50 bg-gray-900 opacity-40"></div>

        <!-- Where the actual content goes -->
        <div class="fixed inset-0 flex items-center z-50 justify-center">

          <!-- Modal body -->
            <chat-box
                :messages="messages"
                :profile="profile"
                @closeChatbox="close($event)"
                class="max-sm:w-[250px] max-h-[600px]"
                :isModal="true"
            />
        </div>
      </div>
      </transition>
    </Teleport>
</template>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 500ms ease-out;
}
</style>
