<script>
import {FwbButton} from "flowbite-vue";
import {useToast} from "vue-toastification";

export default {
  name: "Login",
  components: {FwbButton},
  data(){
    return {
      detum: {
        username: null,
        password: null,
      },
      forget: false,
      changePassword: false,
      password1: null,
      password2: null,
      passErrors:null,
      errors: {
        new_password1: [],
        new_password2: [],
      }
    }
  },
  methods:{
    Login(){
      const params = {
        'username': this.detum.username,
        'password': this.detum.password,
      }
      this.axios.post('/Login/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        if (response.data['status_code']==200){
            const toast = useToast();
            toast.success("ورود با موفقیت انجام شد");
            this.$router.replace({ path: '/'}).then(()=>{
              // location.reload();
            });
        }
      }).catch(()=>{})
    },
    confirm(){
      const params = {
        'username': this.detum.username,
      }
      // this.changePassword = true;
      this.axios.get('/password_reset/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])

        if (response.data['message']=='password_reset_confirm'){
          this.changePassword = true;
           const toast = useToast();
           toast.success("نام کاربری معتبر است!");
        }
      })

    },
    reset(){
      const params = {
        'username': this.detum.username,
        'new_password1': this.password1,
        'new_password2': this.password2
      }
      this.axios.post('/password_reset/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])
        if (response.data['message']=='password_reset_successful'){
          this.forget=false;
          this.changePassword=false
           const toast = useToast();
           toast.success("رمزعبور با موفقیت تغییر یافت");
        }
        if (response.data['message']=='password_reset_errors'){
          const toast = useToast();
           toast.error("خطا در تغییر رمز عبور!");
           this.passErrors = JSON.parse(response.data['data'])
           console.log(this.passErrors)
          // Map backend errors to form fields
          for (const field in this.passErrors) {
            console.log(field)
            if (this.errors.hasOwnProperty(field)) {
              this.errors[field] = this.passErrors[field].map(e => e.message);
            }
          }
        }
      })

    }
  },
}
</script>

<template>
  <div>
    <teleport to="body">
      <section class="fixed inset-0 bg-gray-50 dark:bg-gray-900 z-50">
        <div class="flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen lg:py-0">
            <div class="w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
              <template v-if="forget==false">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                      ورود به پنل مدیریت
                    </h1>
                    <form class="space-y-4 md:space-y-6" action="#">
                        <div>
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">نام کاربری</label>
                            <input v-model="detum.username" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="نام کاربری" required="">
                        </div>
                        <div>
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">کلمه عبور</label>
                            <input v-model="detum.password" type="password" placeholder="••••••••" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                        </div>
                    </form>
                    <button @click="Login" class="w-full text-white bg-blue-600 hover:bg-blue-700 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800">ورود</button>
                    <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <div class="flex flex-row gap-2">
                    <h1 @click="regester=true;" class="text-blue-600 font-medium text-sm hover:text-blue-800 hover:cursor-pointer">ثبت نام</h1>
                    <span  class="text-blue-600 font-medium text-sm">/</span>
                    <h1 @click="forget=true;" class="text-blue-600 font-medium text-sm hover:text-blue-800 hover:cursor-pointer">بازیابی رمز عبور</h1>
                    </div>
                </div>
              </template>
              <template v-else>
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                      بازیابی رمز عبور
                    </h1>
                    <div class="space-y-4 md:space-y-6">
                        <div v-if="changePassword" class="space-y-4 md:space-y-6">
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">رمز عبور جدید</label>
                          <input v-model="password1" type="password" placeholder="••••••••" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                          <ul class="text-sm text-gray-900">
                            <li>رمز عبور شما نباید خیلی شبیه به سایر اطلاعات شخصی شما باشد.</li>
                            <li>رمز عبور شما باید حداقل ۸ کاراکتر داشته باشد.</li>
                            <li>رمز عبور شما نباید یک رمز عبور معمولاً استفاده شده باشد.</li>
                            <li>رمز عبور شما نباید کاملاً عددی باشد.</li>
                          </ul>
                          <div v-if="errors.new_password1" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password1" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">تکرار رمز عبور</label>
                          <input v-model="password2" type="password" placeholder="••••••••" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
<!--                          <p class="text-sm text-gray-900">کد ارسال شده از طرق ربات به اکات تلگرامتان را وارد کنید.</p>-->
                          <div v-if="errors.new_password2" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password2" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <button @click="reset" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800">بازیابی</button>
                        </div>
                        <div v-else class="space-y-4 md:space-y-6">
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">نام کاربری</label>
                            <input v-model="detum.username" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="نام کاربری" required="">
                            <button @click="confirm" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800">تایید</button>
                        </div>
                    </div>
                  <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <h1 @click="forget=false; changePassword=false" class="text-red-600 font-medium text-sm hover:text-red-800 hover:cursor-pointer">یازگشت به صفحه لاگین</h1>
                </div>
              </template>
            </div>
        </div>
      </section>
    </teleport>
  </div>
</template>
