<script>
import ProfilePicture from "@/components/ProfilePicture.vue";
import chatDropdown from "@/components/chat/chatDropdown.vue";

export default {
  name: "Bubble",
  components: {chatDropdown, ProfilePicture},
  props: ['picture', 'enter_id', 'enter_name', 'message', 'datetime', 'image', 'isSender', 'status', 'messageID', 'sender_picture'],
  methods:{
    dropdownEvent(event){
      const name = event[0]
      this.$emit('BubbleEvent', [name, this.messageID, this.message]);

    },
    openImageInNewWindow(imageUrl) {
      window.open(imageUrl, '_blank');
    },
  },
}
</script>

<template>
  <div class="flex items-start gap-2.5"
      :class="[isSender ? 'flex-row-reverse': ' ']">
     <ProfilePicture :username="enter_id" :src="picture"></ProfilePicture>
     <div class="flex flex-col gap-1">
        <div class="flex items-center space-x-2" :class="[isSender ? 'flex-row-reverse': 'rtl:space-x-reverse']">
           <span class="text-sm font-semibold text-gray-900 dark:text-white">{{ enter_name }}</span>
           <span class="text-sm font-normal text-gray-500 dark:text-gray-400">{{ datetime }}</span>
        </div>
        <div class="flex flex-col w-full max-w-[320px] leading-1.5 p-4 border-gray-200 bg-gray-100 rounded-b-xl dark:bg-gray-700"
             :class="[isSender ? 'rounded-tr-xl': 'rounded-tl-xl']">
          <p class="text-sm font-normal text-gray-900 dark:text-white">{{ message }}</p>
          <div class="group relative my-2.5" v-if="image">
              <div class="absolute w-full h-full bg-gray-900/50 opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-lg flex items-center justify-center"
                @click="openImageInNewWindow(image)"
              >
                  <button data-tooltip-target="download-image" class="inline-flex items-center justify-center rounded-full h-10 w-10 bg-white/30 hover:bg-white/50 focus:ring-4 focus:outline-none dark:text-white focus:ring-gray-50">
                      <svg class="w-5 h-5 text-white" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 16 18">
                          <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 1v11m0 0 4-4m-4 4L4 8m11 4v3a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-3"/>
                      </svg>
                  </button>
                  <div id="download-image" role="tooltip" class="absolute z-10 invisible inline-block px-3 py-2 text-sm font-medium text-white transition-opacity duration-300 bg-gray-900 rounded-lg shadow-sm opacity-0 tooltip dark:bg-gray-700">
                      Download image
                      <div class="tooltip-arrow" data-popper-arrow></div>
                  </div>
              </div>
              <img :src="image" class="rounded-lg" />
          </div>
        </div>
        <span class="text-sm font-normal text-gray-500 dark:text-gray-400" :class="[isSender ? 'self-end': '']">
          {{ status }}
        </span>
     </div>
     <chatDropdown v-if="isSender" class="self-center" position-class="left-0" @dropdownEvent="dropdownEvent" title="مرتب سازی" :options="['Reply', 'Delete']"></chatDropdown>
<!--     <chatDropdown class="self-center" :position-class="[isSender ? 'left-0': 'right-0']" @dropdownEvent="dropdownEvent" title="مرتب سازی" :options="[isSender ? ['Reply', 'Delete']: ['Delete']][0]"></chatDropdown>-->
  </div>
</template>
