<script>
import Button from "@/components/button.vue";

export default {

  name: "chatDropdown",
  components: {Button},
  // props:['options', 'left', 'right'],
  props: {
    options: { type: Array, required: true },
    positionClass: { type: String, default: 'right-0' },
  },
  data() {
    return {
      isOptionsExpanded: false,
    };
  },
  methods: {
    setOption(option, target) {
      this.$emit('DropdownEvent', [option, target]);
      this.isOptionsExpanded = false;
    }
  }
};
</script>

<template>
    <div class="relative space-y-2">
      <button @click="isOptionsExpanded = !isOptionsExpanded" @blur="isOptionsExpanded = false"
        class="inline-flex self-center items-center p-2 text-sm font-medium text-center text-gray-900 bg-white rounded-lg hover:bg-gray-100 focus:ring-4 focus:outline-none dark:text-white focus:ring-gray-50 dark:bg-gray-900 dark:hover:bg-gray-800 dark:focus:ring-gray-600"
      >
          <svg class="w-4 h-4 text-gray-500 dark:text-gray-400" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 4 15">
             <path d="M3.5 1.5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0Zm0 6.041a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0Zm0 5.959a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0Z"/>
          </svg>
      </button>
      <transition
        enter-active-class="transform transition duration-500 ease-custom"
        enter-class="-translate-y-1/2 scale-y-0 opacity-0"
        enter-to-class="translate-y-0 scale-y-100 opacity-100"
        leave-active-class="transform transition duration-300 ease-custom"
        leave-class="translate-y-0 scale-y-100 opacity-100"
        leave-to-class="-translate-y-1/2 scale-y-0 opacity-0"
      >
        <ul
          v-show="isOptionsExpanded"
          class="absolute mb-4 overflow-hidden bg-white divide-y divide-gray-100 rounded-lg shadow w-auto dark:bg-gray-700 z-50"
          :class="positionClass"
        >
          <li
            v-for="(option, index) in options"
            :key="index"
            class="block w-full px-4 py-2 text-nowrap border-b border-gray-200 cursor-pointer hover:bg-gray-100 hover:text-blue-700"
            @click="setOption(option, $event.target)"
          >
            {{ option }}
          </li>
        </ul>
      </transition>
    </div>
</template>

<style>
.ease-custom {
  transition-timing-function: cubic-bezier(.61,-0.53,.43,1.43);
}
</style>