<script>

import ProfilePicture from "@/components/ProfilePicture.vue";

export default {
  name: "Lottery",
  components: {ProfilePicture},
  data(){
    return {
      participants: [],
      Socket: null, // WebSocket connection
      connected_users: 0,
      number_of_useres: 0,
      lottery_started: false,
      lottery_winners: null,
      win_waiting:true,
      countDown: 60,
      items: [],
      randomItems: [],
      history: []
    }
  },
  methods: {
    getParticipantsOFLottery(){
      this.axios.get('/api/getParticipantsOFLottery').then((response) => {
        this.participants = response.data['data'][0]
        this.number_of_useres = response.data['data'][1]
        // console.log(response.data['data'])
      })
    },
    getLotteryHistory(){
      this.axios.get('/api/getLotteryHistory').then((response) => {
        // console.log('getLotteryHistory\n', response.data['data'])
        this.history = response.data['data']
      })
    },
    countDownTimer () {
      // console.log('countDownTimer lottery_started', this.lottery_started)
        if (this.countDown > 0) {
          if (this.lottery_started) {
            setTimeout(() => {
              this.countDown -= 1
              this.countDownTimer()
              // this.shuffleItems()
              this.selectRandomItems()
            }, 1000)
          }
        }
        else {
          this.win_waiting=false
        }
    },
    shuffleItems() {
      this.items = this.items
        .map(value => ({ value, sort: Math.random() }))
        .sort((a, b) => a.sort - b.sort)
        .map(({ value }) => value);
    },
    selectRandomItems() {
      const shuffledArray = [...this.participants]; // Create a copy of the original array
      for (let i = shuffledArray.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffledArray[i], shuffledArray[j]] = [shuffledArray[j], shuffledArray[i]]; // Fisher-Yates shuffle
      }
      this.randomItems = shuffledArray.slice(0, 3); // Select the first 3 items from the shuffled array

    },
  },
  mounted() {
    this.getParticipantsOFLottery()
    this.getLotteryHistory()
    // console.log(window.Telegram.WebApp)

    // Initialize WebSocket connection
    this.Socket = new WebSocket('ws://' + window.location.host + '/ws/lottery/');
    this.Socket.onopen = function(event) {
      console.log('WebSocket connection opened');
    };
    // Set up message handler
    this.Socket.onmessage = (e) => {
      const data = JSON.parse(e.data);
      console.log(data);
      if (data.message) {
          // Handle incoming messages from the server
          let message = JSON.parse(data.message)
          // console.log('Received message:', message);
          if (message.connected_users){
            this.connected_users = message.connected_users
            // console.log('connected_users is:', this.connected_users)
          }
          if (message.status){
            this.lottery_started = message.status
            this.lottery_winners = message.winners
            // console.log('lottery status is:', this.lottery_started)
            setTimeout(() => {this.countDownTimer()}, 1000)
          }

      }
    };

    // Handle WebSocket errors
    this.Socket.onerror = (error) => {
      console.error('WebSocket Error: ', error);
    };


  },
  beforeDestroy() {
    // Close WebSocket connection when component is destroyed
    if (this.Socket) {
      this.Socket.close();
    }
  },
}
</script>

